.profile__title {
  max-width: 294px;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 1024px) {
  .profile__title {
    max-width: 189px;
    font-size: 27px;
    line-height: 33px;
    margin-top: 23px;
    text-align: center;
  }
}
